import { timer } from 'd3-timer';


function clamp(min, max) {
  return function(v) {
    return v < min ? min :  v > max ? max : v;
  }
}

export class ScrollMotion {
  /**
   * @param {HTMLElement} dom 
   */
  constructor(dom) {
    this.movables = Array.prototype.slice.call(dom.children, 0);
    dom.addEventListener('touchstart', this.movestart.bind(this));
    dom.addEventListener('touchmove', this.moving.bind(this));
    dom.addEventListener('touchend', this.moveend.bind(this));
    this.bound = {
      top: 0,
      bottom: dom.scrollHeight - dom.clientHeight
    };
    this.height = dom.clientHeight;
    this.cut = clamp(-this.bound.bottom, 0);
    this.domNode = dom;
    this.startTouchPos = 0;
    this.lastPos = 0;
    this.curPos = 0;
    this.lastTouchPos = 0;
    this.touchPos = 0;
    this.offset = 0;
    this.mass = 1;
    this.force = 0;
    this.friction = 8;
    this.updateAcceleratedSpeed();
    this.FrictionDirection = 1;
    this.lastTime = this.curTime =  Date.now();
    this.dragging = false;
    this.curSpeed = 0;
    this.hasDragged = false;

    timer((() => {
      let lastMoment = 0;
      return elapsed => {
        this.update(elapsed - lastMoment);
        lastMoment = elapsed;
      };
    })());
  }
  get inBound() {
    return !(this.arriveTop || this.arriveBottom);
  }
  get isDraggingUp() {
    return this.touchPos - this.lastTouchPos < 0;
  }
  get isDraggingDown() {
    console.log(this.touchPos - this.lastTouchPos > 0);
    return this.touchPos - this.lastTouchPos > 0;
  }
  get arriveTop() {
    return this.curPos >= 0;
  }
  get arriveBottom() {
    return this.curPos <= -this.bound.bottom;
  }
  updateAcceleratedSpeed() {
    this.acceleratedSpeed = this.friction / this.mass;
  }
  /**
   * @param {Event} e 
   */
  movestart(e) {
    this.curSpeed = 0;
    this.dragging = true;
    const touch = e.touches[0];
    this.startTouchPos = this.lastPos = touch.clientY;
    this.curTime = Date.now();
  }
  /**
   * @param {Event} e 
   */
  moving(e) {
    e.preventDefault();
    this.hasDragged = true;
    const touch = e.touches[0];
    this.lastPos = this.curPos;
    this.lastTouchPos = this.touchPos;
    this.lastTime = this.curTime;
    this.curTime = Date.now();
    this.touchPos = touch.clientY;
    if (this.arriveTop && this.isDraggingDown) {
      console.log('b');
    } else {
      this.curPos = this.calcCurrentPos(touch);
      this.updatePos(this.curPos);
    }
  }
  moveend(e) {
    if (this.hasDragged) {
      this.offset = this.curPos;
      // debugger;
      this.curSpeed = (this.curPos - this.lastPos) / (this.curTime - this.lastTime);
      console.log(this.curSpeed);
      this.FrictionDirection = this.curSpeed < 0 ? 1 : -1;
    }
    this.hasDragged = false;
    this.dragging = false;
  }
  get elasticForce() {
    let force = 2;
    if (this.curPos > 0) {
      const f = Math.pow((this.curPos / 100) * force, 3) * -1;
      console.log(`percent:`, this.curPos / (this.height / 4))
      console.log(`f: ${f}`);
      return f;
    }
  }
  update(deltaTime) {
    if (this.curSpeed === 0) return;
    const factor = deltaTime / 1000;
    if (this.arriveTop) {
      if (this.dragging) return;
      this.curSpeed = this.curSpeed + (this.elasticForce / this.mass) * factor;
      console.log(this.curSpeed);
    } else {
      if (this.curSpeed === 0) return;
      if (this.FrictionDirection > 0) {
        this.curSpeed = Math.min(0, this.curSpeed + this.acceleratedSpeed * this.FrictionDirection * factor);
      } else {
        this.curSpeed = Math.max(0, this.curSpeed + this.acceleratedSpeed * this.FrictionDirection * factor);
      }
    }

    this.curPos += this.curSpeed * deltaTime;
    this.offset = this.curPos;
    this.updatePos(this.curPos);
  }
  calcCurrentPos(touch) {
    const pos = touch.clientY;
    const distance  = pos - this.startTouchPos;
    return this.cut(this.offset + distance);
  }
  updatePos(pos) {
    this.movables.forEach((e) => {
      e.style.transform = `translateY(${pos}px)`;
    });
  }
}


